export const getValidateUser = (params) => {
  return {
    type: VALIDATE_USER,
    payload: params,
  };
};

export const getValidateUserSuccess = () => {
  return {
    type: VALIDATE_USER_SUCCESS,
  };
};

export const getValidateUserFail = (error) => {
  return {
    type: VALIDATE_USER_FAIL,
    payload: error,
  };
};

